import React from "react";

// reactstrap components
import {
    Col,
  } from "reactstrap";

const Image = (props) => {
    const id = props.id
    return(
        <Col width="50%">
            <img
                alt="..."
                className="img-raised"
                src={require("../../assets/img/pictures/"+id+".jpg")}
                width='80%'
            ></img>
        </Col>
    )
}

export default Image;